'use client';

import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useAuth } from '@/contexts/AuthContext';
import {
  Squares2X2Icon,
  ChatBubbleLeftRightIcon,
  DocumentTextIcon,
  BellIcon,
  CreditCardIcon,
  UserIcon,
  ArrowPathIcon,
  ClockIcon,
  CheckCircleIcon,
  QuestionMarkCircleIcon,
  XCircleIcon
} from '@heroicons/react/24/outline';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

// أنواع حالة الطلب
enum RequestStatus {
  PENDING = 'pending',
  IN_PROGRESS = 'in_progress',
  COMPLETED = 'completed',
  CANCELLED = 'cancelled'
}

// نوع بيانات الطلب
interface ServiceRequest {
  id: string;
  title: string;
  category: string;
  price: number;
  status: RequestStatus;
  date: string;
  providerName?: string;
  providerImage?: string;
}

// نوع بيانات الإشعار
interface Notification {
  id: string;
  message: string;
  date: string;
  read: boolean;
  type: 'info' | 'success' | 'warning' | 'error';
}

export default function DashboardPage() {
  const { user, isAuthenticated } = useAuth();
  const [activeTab, setActiveTab] = useState('overview');

  // بيانات تجريبية للطلبات
  const serviceRequests: ServiceRequest[] = [
    {
      id: 'req-1',
      title: 'تصميم صور بواسطة Midjourney',
      category: 'توليد صور',
      price: 150,
      status: RequestStatus.IN_PROGRESS,
      date: '2023-10-15',
      providerName: 'أحمد محمود',
      providerImage: '/images/providers/provider1.jpg'
    },
    {
      id: 'req-2',
      title: 'كتابة مقال عن الذكاء الاصطناعي',
      category: 'توليد نصوص',
      price: 75,
      status: RequestStatus.COMPLETED,
      date: '2023-10-10',
      providerName: 'سارة أحمد',
      providerImage: '/images/providers/provider2.jpg'
    },
    {
      id: 'req-3',
      title: 'إنشاء نموذج تعلم آلي مخصص',
      category: 'تدريب نماذج',
      price: 500,
      status: RequestStatus.PENDING,
      date: '2023-10-20'
    }
  ];

  // بيانات تجريبية للإشعارات
  const notifications: Notification[] = [
    {
      id: 'notif-1',
      message: 'تم قبول طلب تصميم الصور الخاص بك',
      date: '2023-10-16',
      read: false,
      type: 'success'
    },
    {
      id: 'notif-2',
      message: 'اكتمل طلب كتابة المقال، يرجى مراجعته',
      date: '2023-10-12',
      read: true,
      type: 'info'
    },
    {
      id: 'notif-3',
      message: 'تحديث في سياسة الخصوصية للمنصة',
      date: '2023-10-05',
      read: true,
      type: 'warning'
    }
  ];

  // إحصائيات المستخدم
  const userStats = {
    completedRequests: 8,
    activeRequests: 2,
    totalSpent: 1250,
    averageRating: 4.8
  };

  // مكون لعرض حالة الطلب
  const StatusBadge = ({ status }: { status: RequestStatus }) => {
    let badgeClass = '';
    let statusText = '';
    let StatusIcon = QuestionMarkCircleIcon;

    switch (status) {
      case RequestStatus.PENDING:
        badgeClass = 'bg-yellow-100 text-yellow-800';
        statusText = 'بانتظار الموافقة';
        StatusIcon = ClockIcon;
        break;
      case RequestStatus.IN_PROGRESS:
        badgeClass = 'bg-blue-100 text-blue-800';
        statusText = 'قيد التنفيذ';
        StatusIcon = ArrowPathIcon;
        break;
      case RequestStatus.COMPLETED:
        badgeClass = 'bg-green-100 text-green-800';
        statusText = 'مكتمل';
        StatusIcon = CheckCircleIcon;
        break;
      case RequestStatus.CANCELLED:
        badgeClass = 'bg-red-100 text-red-800';
        statusText = 'ملغي';
        StatusIcon = XCircleIcon;
        break;
    }

    return (
      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${badgeClass}`}>
        <StatusIcon className="ml-1 h-4 w-4" />
        {statusText}
      </span>
    );
  };

  // مكون لعرض نوع الإشعار
  const NotificationIcon = ({ type }: { type: string }) => {
    switch (type) {
      case 'success':
        return <CheckCircleIcon className="h-5 w-5 text-green-500" />;
      case 'warning':
        return <BellIcon className="h-5 w-5 text-yellow-500" />;
      case 'error':
        return <XCircleIcon className="h-5 w-5 text-red-500" />;
      default:
        return <BellIcon className="h-5 w-5 text-blue-500" />;
    }
  };

  // إذا لم يكن المستخدم مسجل الدخول
  if (!isAuthenticated) {
    return (
      <div className="min-h-screen flex flex-col bg-gray-50">
        <Navbar />
        <main className="flex-grow flex items-center justify-center">
          <div className="text-center">
            <h1 className="text-2xl font-bold text-gray-900 mb-4">يجب تسجيل الدخول أولاً</h1>
            <p className="text-gray-600 mb-6">يرجى تسجيل الدخول للوصول إلى لوحة التحكم</p>
            <Link
              href="/login"
              className="inline-flex items-center px-4 py-2 border border-transparent text-base font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              تسجيل الدخول
            </Link>
          </div>
        </main>
        <Footer />
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Navbar />
      
      <main className="flex-grow py-10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mb-8">
            <h1 className="text-2xl font-bold text-gray-900">مرحباً، {user?.name}</h1>
            <p className="mt-1 text-sm text-gray-600">
              {user?.accountType === 'client' ? 'حساب عميل' : 'حساب مزود خدمة'}
            </p>
          </div>
          
          {/* إحصائيات المستخدم */}
          <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4 mb-8">
            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <CheckCircleIcon className="h-6 w-6 text-green-600" />
                  </div>
                  <div className="mr-5">
                    <p className="text-sm font-medium text-gray-500">الطلبات المكتملة</p>
                    <p className="text-xl font-semibold text-gray-900">{userStats.completedRequests}</p>
                  </div>
                </div>
              </div>
            </div>
            
            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <ArrowPathIcon className="h-6 w-6 text-blue-600" />
                  </div>
                  <div className="mr-5">
                    <p className="text-sm font-medium text-gray-500">الطلبات النشطة</p>
                    <p className="text-xl font-semibold text-gray-900">{userStats.activeRequests}</p>
                  </div>
                </div>
              </div>
            </div>
            
            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <CreditCardIcon className="h-6 w-6 text-indigo-600" />
                  </div>
                  <div className="mr-5">
                    <p className="text-sm font-medium text-gray-500">المبالغ المدفوعة</p>
                    <p className="text-xl font-semibold text-gray-900">{userStats.totalSpent} ر.س</p>
                  </div>
                </div>
              </div>
            </div>
            
            <div className="bg-white overflow-hidden shadow rounded-lg">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <UserIcon className="h-6 w-6 text-yellow-600" />
                  </div>
                  <div className="mr-5">
                    <p className="text-sm font-medium text-gray-500">متوسط التقييم</p>
                    <p className="text-xl font-semibold text-gray-900">{userStats.averageRating}/5</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          {/* أزرار التنقل */}
          <div className="border-b border-gray-200 mb-8">
            <nav className="-mb-px flex space-x-8 space-x-reverse">
              <button
                onClick={() => setActiveTab('overview')}
                className={`${
                  activeTab === 'overview'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                <Squares2X2Icon className="ml-2 -mt-1 w-5 h-5 inline-block" />
                نظرة عامة
              </button>
              <button
                onClick={() => setActiveTab('requests')}
                className={`${
                  activeTab === 'requests'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                <DocumentTextIcon className="ml-2 -mt-1 w-5 h-5 inline-block" />
                الطلبات
              </button>
              <button
                onClick={() => setActiveTab('messages')}
                className={`${
                  activeTab === 'messages'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                <ChatBubbleLeftRightIcon className="ml-2 -mt-1 w-5 h-5 inline-block" />
                الرسائل
              </button>
              <button
                onClick={() => setActiveTab('notifications')}
                className={`${
                  activeTab === 'notifications'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                <BellIcon className="ml-2 -mt-1 w-5 h-5 inline-block" />
                الإشعارات
              </button>
            </nav>
          </div>
          
          {/* محتوى علامات التبويب */}
          <div className="bg-white shadow rounded-lg p-6">
            {activeTab === 'overview' && (
              <div>
                <h2 className="text-lg font-medium text-gray-900 mb-6">المهام النشطة</h2>
                <div className="space-y-4">
                  {serviceRequests.filter(req => req.status === RequestStatus.IN_PROGRESS).map(request => (
                    <div key={request.id} className="bg-gray-50 p-4 rounded-md flex justify-between items-center">
                      <div>
                        <h3 className="font-medium">{request.title}</h3>
                        <div className="flex items-center mt-1">
                          <span className="text-sm text-gray-500 ml-3">{request.category}</span>
                          <StatusBadge status={request.status} />
                        </div>
                      </div>
                      <Link
                        href={`/service-requests/${request.id}`}
                        className="text-indigo-600 hover:text-indigo-500 text-sm font-medium"
                      >
                        عرض التفاصيل
                      </Link>
                    </div>
                  ))}
                </div>

                <div className="mt-8">
                  <h2 className="text-lg font-medium text-gray-900 mb-6">آخر الإشعارات</h2>
                  <div className="space-y-4">
                    {notifications.slice(0, 3).map(notification => (
                      <div 
                        key={notification.id} 
                        className={`p-4 rounded-md flex items-start ${notification.read ? 'bg-gray-50' : 'bg-blue-50'}`}
                      >
                        <div className="flex-shrink-0 mt-0.5 ml-3">
                          <NotificationIcon type={notification.type} />
                        </div>
                        <div className="flex-1">
                          <p className={`text-sm ${notification.read ? 'text-gray-600' : 'text-gray-900 font-medium'}`}>
                            {notification.message}
                          </p>
                          <p className="text-xs text-gray-500 mt-1">{notification.date}</p>
                        </div>
                      </div>
                    ))}
                  </div>

                  {notifications.length > 3 && (
                    <div className="mt-4 text-center">
                      <button
                        onClick={() => setActiveTab('notifications')}
                        className="text-sm text-indigo-600 hover:text-indigo-500"
                      >
                        عرض كل الإشعارات
                      </button>
                    </div>
                  )}
                </div>
              </div>
            )}
            
            {activeTab === 'requests' && (
              <div>
                <div className="flex justify-between items-center mb-6">
                  <h2 className="text-lg font-medium text-gray-900">جميع الطلبات</h2>
                  <Link
                    href="/service-request"
                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700"
                  >
                    طلب خدمة جديدة
                  </Link>
                </div>
                <div className="overflow-hidden shadow ring-1 ring-black ring-opacity-5 md:rounded-lg">
                  <table className="min-w-full divide-y divide-gray-300">
                    <thead className="bg-gray-50">
                      <tr>
                        <th scope="col" className="py-3.5 pr-4 pl-3 text-right text-sm font-semibold text-gray-900">
                          العنوان
                        </th>
                        <th scope="col" className="px-3 py-3.5 text-right text-sm font-semibold text-gray-900">
                          التصنيف
                        </th>
                        <th scope="col" className="px-3 py-3.5 text-right text-sm font-semibold text-gray-900">
                          السعر
                        </th>
                        <th scope="col" className="px-3 py-3.5 text-right text-sm font-semibold text-gray-900">
                          الحالة
                        </th>
                        <th scope="col" className="px-3 py-3.5 text-right text-sm font-semibold text-gray-900">
                          التاريخ
                        </th>
                        <th scope="col" className="relative py-3.5 pl-4 pr-3">
                          <span className="sr-only">عرض</span>
                        </th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {serviceRequests.map(request => (
                        <tr key={request.id}>
                          <td className="py-4 pr-4 pl-3 text-sm text-gray-900 font-medium">
                            {request.title}
                          </td>
                          <td className="px-3 py-4 text-sm text-gray-500">
                            {request.category}
                          </td>
                          <td className="px-3 py-4 text-sm text-gray-500">
                            {request.price} ر.س
                          </td>
                          <td className="px-3 py-4 text-sm text-gray-500">
                            <StatusBadge status={request.status} />
                          </td>
                          <td className="px-3 py-4 text-sm text-gray-500">
                            {request.date}
                          </td>
                          <td className="py-4 pl-4 pr-3 text-right text-sm font-medium">
                            <Link
                              href={`/service-requests/${request.id}`}
                              className="text-indigo-600 hover:text-indigo-900"
                            >
                              عرض
                            </Link>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}
            
            {activeTab === 'messages' && (
              <div className="text-center py-20">
                <div className="inline-block p-6 bg-gray-100 rounded-full mb-4">
                  <ChatBubbleLeftRightIcon className="h-10 w-10 text-gray-400" />
                </div>
                <h2 className="text-xl font-medium text-gray-900 mb-2">لا توجد رسائل حالياً</h2>
                <p className="text-gray-500 max-w-md mx-auto">
                  عندما تبدأ محادثة مع أحد مزودي الخدمات أو العملاء، ستظهر هنا.
                </p>
                <Link
                  href="/providers"
                  className="mt-6 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700"
                >
                  استعراض مزودي الخدمات
                </Link>
              </div>
            )}
            
            {activeTab === 'notifications' && (
              <div>
                <h2 className="text-lg font-medium text-gray-900 mb-6">الإشعارات</h2>
                {notifications.length === 0 ? (
                  <div className="text-center py-20">
                    <div className="inline-block p-6 bg-gray-100 rounded-full mb-4">
                      <BellIcon className="h-10 w-10 text-gray-400" />
                    </div>
                    <h2 className="text-xl font-medium text-gray-900 mb-2">لا توجد إشعارات</h2>
                    <p className="text-gray-500">
                      سيتم إشعارك عند وجود تحديثات على طلباتك أو عند تلقي رسائل جديدة.
                    </p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {notifications.map(notification => (
                      <div 
                        key={notification.id} 
                        className={`p-4 rounded-md flex items-start ${notification.read ? 'bg-gray-50' : 'bg-blue-50'}`}
                      >
                        <div className="flex-shrink-0 mt-0.5 ml-3">
                          <NotificationIcon type={notification.type} />
                        </div>
                        <div className="flex-1">
                          <p className={`text-sm ${notification.read ? 'text-gray-600' : 'text-gray-900 font-medium'}`}>
                            {notification.message}
                          </p>
                          <p className="text-xs text-gray-500 mt-1">{notification.date}</p>
                        </div>
                        {!notification.read && (
                          <button
                            type="button"
                            className="mr-2 inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded text-indigo-700 bg-indigo-100 hover:bg-indigo-200 focus:outline-none"
                          >
                            تمييز كمقروء
                          </button>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </main>
      
      <Footer />
    </div>
  );
} 